body {
  font-family: 'Walter Turncoat', cursive;
  text-align: center;
}

.App-title {
  padding: 10px;
	margin: 0;
	font-size: 40px;
  display: inline-block;
  color: white;
  background: -webkit-gradient(linear, left top, right top, from(red), color-stop(orange) , color-stop(yellow), color-stop(limegreen), color-stop(cyan), to(violet));
  background: -webkit-linear-gradient(left, red, orange , yellow, limegreen, cyan, violet);
  background: -o-linear-gradient(left, red, orange , yellow, limegreen, cyan, violet);
  background: linear-gradient(to right, red, orange , yellow, limegreen, cyan, violet);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}




